<!--教材管理  教材详情-->
<template>
    <div class="page-template">
         <!--页面title-->
         <div class="org-set-rnt-title"><span class="cha-title">{{ title }}</span>><span>  {{ viceTitel }}</span></div> 
             

        <!-- tab布局-->
        <div class="main-box">
                <el-tabs v-model="activeName" @tab-click="handleClick" class="tab-box" >                      
                        <el-tab-pane label="教材信息" name="1">
                        <el-form :model="ruleForm" ref="textBookinfoId" label-width="80px" class="demo-ruleForm demo-form-inline" :rules="formRules">                                
                                <el-form-item label="名称:" prop="uservalue" class="labename">
                                    <span v-show="!editSwitch">{{ bookinfodata.contentName }}</span>                             
                                    <el-select v-model="uservalue" placeholder="请选择" v-show="editSwitch" disabled class="icon-none">
                                    <el-option      
                                        multiple
                                        filterable                         
                                        v-for="item in username"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>    
                                    </el-select>      
                                </el-form-item>

                                <el-form-item label="学段:" prop="phasevalue" class="labename">
                                    <span v-show="!editSwitch">{{ bookinfodata.periodIdText }}</span>
                                    <el-select v-model="ruleForm.phasevalue" placeholder="请选择"  v-show="editSwitch"  @change="getPhaseValue">
                                        <el-option
                                        v-for="item in ruleForm.phase"
                                        :key="item.code"
                                        :label="item.text"
                                        :value="item.code">
                                        </el-option>
                                    </el-select>
                                </el-form-item>

                                <el-form-item label="学科:" prop="subjectValue" class="labename">
                                    <span v-show="!editSwitch">{{ bookinfodata.subjectIdName }}</span>
                                    <el-select v-model="ruleForm.subjectValue" placeholder="请选择"  v-show="editSwitch"  @change="getSubjectsValue">
                                            <el-option
                                            v-for="item in ruleForm.subjectData"
                                            :key="item.id"
                                            :label="item.name"
                                            :value="item.id">
                                            </el-option>
                                        </el-select>
                                </el-form-item>


                                <el-form-item label="版本:" prop="versionValue" class="labename">
                                    <span v-show="!editSwitch">{{ bookinfodata.textbookVersionIdName }}</span>
                                <el-select v-model="ruleForm.versionValue" placeholder="请选择"  v-show="editSwitch">
                                        <el-option
                                        v-for="item in ruleForm.versionData"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>

                                <el-form-item label="年级:" prop="gradeValue" class="labename">
                                    <span v-show="!editSwitch">{{ bookinfodata.gradeIdText }}</span>
                                    <el-select v-model="ruleForm.gradeValue" placeholder="请选择"  v-show="editSwitch"  @change="getgradeSelectValue">
                                        <el-option
                                        v-for="item in ruleForm.gradeData"
                                        :key="item.code"
                                        :label="item.text"
                                        :value="item.code">
                                        </el-option>
                                    </el-select>
                                </el-form-item>

                                <el-form-item label="分册:" prop="volumeValue" class="labename">
                                    <span v-show="!editSwitch">{{bookinfodata.sheetsTypeText}}</span>
                                    <el-select v-model="ruleForm.volumeValue" placeholder="请选择"  v-show="editSwitch"  @change="getvolumeValue">
                                            <el-option
                                            v-for="item in ruleForm.volumedata"
                                            :key="item.code"
                                            :label="item.text"
                                            :value="item.code">
                                            </el-option>
                                    </el-select>
                                </el-form-item>

                                <el-form-item label="备注:" prop="name" class="labename">
                                    <span v-show="!editSwitch">{{bookinfodata.notes}}</span>
                                    <el-input v-show="editSwitch" v-model="ruleForm.notesValue" class="limitlabewidth"></el-input>                                       
                                </el-form-item>
                                
    
                                <el-row class="saveBtn" >                 
                                    <el-button type="primary" @click="getSave('textBookinfoId')"  v-show="editSwitch">保存</el-button>   
                                </el-row> 

                        </el-form>
                        </el-tab-pane>
                        
                        <!--  教材目录 第二个tab选项-->
                        <el-tab-pane label="教材目录" name="2" style="height:100%">
                            <div class="org-set">
                                <div class="org-set-left">
                                    <tree :treeData="treeLeftData" :defaultProps="defaultProps" @list-Tree="listTree"></tree>    
                                </div>

                                    <div class="org-set-rnt">
                                        <ul  v-if="bookcatalo" class="right-deatil">
                                            <li class="item-box">
                                                <span class="left-Field">类型:</span>     <span class="right-Content">{{BookDetailData.catalogTypeText}}</span>  
                                            </li>
                                            <li class="item-box">
                                                <span class="left-Field">上级:</span>     <span class="right-Content">{{BookDetailData.parentName}}</span>  
                                            </li>
                                            <li class="item-box">
                                                <span class="left-Field">名称:</span>     <span class="right-Content">{{BookDetailData.catalogName}}</span>  
                                            </li> 
                                            <li class="item-box">
                                                <span class="left-Field">排序:</span>     <span class="right-Content">{{BookDetailData.orderIndex}}</span>  
                                            </li>  
                                            <li class="item-box">
                                                <span class="left-Field">知识点:</span>     <span class="right-Content"><a v-for="(item,index) in BookDetailData.pointsId" :key="index"> {{item.knowledgePointsName}}</a></span>  
                                            </li> 
                                        </ul>
                                        
                                         <el-form ref="bookmoduleId" :model="form" label-width="80px" :rules="bookmoduleRules" v-else>                                                              
                                            <el-form-item label="类型:" prop="typeValue">
                                                <el-select v-model="form.typeValue" placeholder="请选择" >
                                                    <el-option
                                                        v-for="item in typeData"
                                                        :key="item.code"
                                                        :label="item.text"
                                                        :value="item.code">
                                                    </el-option>
                                                </el-select>
                                            </el-form-item>

                                            <el-form-item label="上级:" prop="superiorValue">
                                                <el-select v-model="form.superiorValue" placeholder="请选择">
                                                    <el-option
                                                    v-for="item in superiorData"
                                                    :key="item.id"
                                                    :label="item.name"
                                                    :value="item.id">
                                                    </el-option>
                                                </el-select>
                                            </el-form-item>   

                                            <el-form-item label="名称:"  prop="name">
                                                <el-input v-model="form.name" class="limtwidth"></el-input>
                                            </el-form-item>

                                            <el-form-item label="排序:">
                                                <el-input v-model="form.order" class="limtwidth"></el-input>
                                            </el-form-item>
                                            
                                            <el-form-item label="知识点:">  
                                                <el-select
                                                    v-model="valuePoint"
                                                    multiple                                            
                                                    placeholder="请选择">
                                                    <el-option
                                                        v-for="item in knowData"
                                                        :key="item.id"
                                                        :label="item.name"
                                                        :value="item.id">
                                                    </el-option>
                                                </el-select>

                                            </el-form-item>

                                            <el-form-item>
                                                <el-button class="form-save" type="primary" @click="onSave('bookmoduleId')">保存</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </div>

                               </div>
                        </el-tab-pane> 


                        <div class="empty"></div>              
                </el-tabs>

                <!--右上角的按钮-->        
                <div class="org-set-rnt-bott">
                    <btnList :model="modelEditor" @btn-click="Editor(0)" v-show="editorFlag"></btnList>
                    <btnList :model="modelAdd" @btn-click="Add(1)" v-show="addFlag"></btnList>
                    <btnList :model="modelDisable" @btn-click="Disable" v-show="disableFlag"></btnList>
                </div>
        </div>               
    </div>
</template>    

<script>
import tree from "../../../components/tree"
import btnList from  '../../../components/btn-list'
export default {
    data() {
        return {
            title:'教材管理',
            viceTitel:'详情',
            activeName: '1',
            editSwitch:false, //控制 下拉框与 详情是否展示  false 消失，为描述。 true为选项
            ruleForm: {
                name: '',
                Jane:'',
                orgId:'',
                region: '',
                sorting:'',
                //教育局
                countyArea:'',
                //学校的数据
                schoolPhase:'',
                schoolArea:'',
                schoolAccount:'',
                schoolPass:'',
                schoolWill:'',
                eduAreaId:'',
                schoolDistrict:'',
                //校区
                campusPhase:'',
                campusArea:'',
                campusDistrict:'',

                 // 教材信息下拉
                id:"",//修改的那一条数据
                phase:[],//学段
                phasevalue:"",
                phasevalueName:"", 
                subjectData:[],//学科
                subjectValue:"",
                subjectValueName:"",
                versionData:[],//版本
                versionValue:"",
                gradeData:[],//年级
                gradeValue:"",
                gradeValueName:"",
                notesValue:"",
                volumedata:[],
                volumeValue:"",//分册
                volumeValueName:"",
            },
            uservalue: '',
            // 教材信息显示 
             bookinfodata:{},
            // 验证
            formRules:{
                phasevalue:[{required: true, message: '请选择学段', trigger:'change'}],
                subjectValue:[{required: true, message: '请选择学科', trigger: 'change'}],
                versionValue:[{required: true, message: '请选择版本', trigger: 'change'}],
                gradeValue:[{required: true, message: '请选择年级', trigger: 'change'}],
                volumeValue:[{required: true, message: '请选择分册', trigger: 'change'}],
            },
            textBookinfoId:{},
            bookmoduleId:{},
            bookmoduleid:"",
            username: [],
            value:"",//临时要改
            regionaShow:false, // 展示树形结构 false显示
            knowData:[],
             // 树形结构中的数据
               treeLeftData:[],
              superiorsdata:[],
              defaultProps: {
                    children: "children",
                    label: "name"
              },
                //  教材目录下的数据
               form: {
                 typeValue:'',//类型
                 name: '',
                 order:'',
                 region: '',
                 superiorValue:'',
                },     
                bookmoduleRules:{
                    typeValue:[{required: true, message: '请选择类型', trigger:'change'}],
                    superiorValue:[{required: true, message: '请选择上级', trigger: 'change'}],
                    name:[{required: true, message: '请选择名称', trigger: 'change'}],                  
                },
                typeData:[{
                  text:"语文",
                  code:"01",
                },{
                  text:"单元组",
                  code:"02",
                }],  
                value: '',   
                valuePoint: [],
                superiorData:[],
                bookcatalo:true,//true列表显示
                treestate:"",//教材内容0-起/1-停状态, 默认 0 启用
                BookDetailData:{},
                //右上角菜单
                modelEditor:{
                    name:'编辑',
                    icon:'icon-edit'
                },
                modelAdd:{
                    name:'添加',
                    icon:'icon-add'
                },
                modelDisable:{
                    name:'禁用',
                    icon:'icon-stop'
                },

                editorFlag:true,
                addFlag:false,
                disableFlag:false,
                emtpyarr:[],//搜集知识点的name
                pointarr:[],//知识点的数据格式

        }
    },

    components: {
        tree: tree,
        btnList,
    },

    created() {
        
          // 编辑传递过来的参数
          if(this.$route.query.editSwitch==true){
              this.editSwitch=this.$route.query.editSwitch;
              this.ruleForm.id=this.$route.query.id;
              this.editorFlag=this.$route.query.editorFlag;
          }else{          
             this.ruleForm.id=this.$route.query.id;       
          }
       
       
      this.dictionary();
      this.subjectSelect();
      this.versionApiData();
      this.getInfoData();
      this.getapiTreeData();
     

      setTimeout(()=>{
         this.getapiTreeSelectpointData();
      },500);

      setTimeout(()=>{
         this.getSuperiorData();
      },700);

       
    },
    methods: {
      //  教材信息--下拉
       // 学段stage  年级grade  分册sheetsType
        dictionary(){
            let params = {"type": ['stage','grade','sheetsType']};
            this.$api.dictSelect(params).then(res => {
                if(res.success == true){
                    let  i = 0;
                    for (i = 0; i < res.data.length; i++) {
                        if(res.data[i].type == 'stage'){
                             this.ruleForm.phase = res.data[i].data;
                        }else if(res.data[i].type == 'grade'){
                              this.ruleForm.gradeData = res.data[i].data;   
                                 
                        }else if(res.data[i].type == 'sheetsType'){
                             this.ruleForm.volumedata = res.data[i].data;   
                                  console.log(this.ruleForm.volumedata)
                        }
                    }
                }
            });
        },  

         //学科下拉框数据
        subjectSelect(){
            let params = {};
            this.$api.subjectSelect(params).then(res => {
                if (res.success == true) {
                   this.ruleForm.subjectData=res.data;
                   console.log(this.ruleForm.subjectData)
                }
            })
        },

         //版本下拉框数据
        versionApiData(){
          this.$api.versionSelect().then(res=>{           
            if (res.success == true) {
               this.ruleForm.versionData=res.data;  

            }
          })
        },

        getgradeSelectValue(value){
             let obj={};
              obj= this.ruleForm.gradeData.find((item)=>{
                return item.code==value;
              });
              this.ruleForm.gradeValueName=obj.text;
        },
        //学段
        getPhaseValue(value){
              let obj={};
              obj= this.ruleForm.phase.find((item)=>{
                return item.code==value;
              });
              console.log(value)
              this.ruleForm.phasevalueName=obj.text;
            
        },
        getvolumeValue(value){
            let obj={};
              obj= this.ruleForm.volumedata.find((item)=>{
                return item.code==value;
              });
              this.ruleForm.volumeValueName=obj.text;
        },

        getSubjectsValue(value){
              let obj={};
              obj= this.ruleForm.subjectData.find((item)=>{
                return item.id==value;
              });
              this.ruleForm.subjectValueName=obj.name;
        },
        
        getSave(textBookinfoId){
                let _this = this;
               _this.$refs[textBookinfoId].validate((valid) => {
                if (valid) {
                    let params={
                        id:_this.ruleForm.id,
                        gradeId:_this.ruleForm.gradeValue,
                        gradeIdText:_this.bookinfodata.periodIdText,
                        notes:_this.ruleForm.notesValue,
                        periodId:_this.ruleForm.phasevalue,
                         periodIdText:_this.bookinfodata.periodIdText,
                        sheetsType:_this.ruleForm.volumeValue,
                        sheetsTypeText:_this.bookinfodata.sheetsTypeText,
                        subjectId:_this.ruleForm.subjectValue,
                        subjectIdName:_this.bookinfodata.subjectIdName,
                        textbookVersionId:_this.ruleForm.versionValue
                    };
                    _this.$api.TextbookInfoSave(params).then(res=>{
                        if(res.success==true){
                            _this.$myLayer.successLayer(res.msg)
                            _this.getInfoData();
                            _this.editSwitch=false;
                            _this.editorFlag=true;                          
                        }
                    })
                } else {
                    console.log('error submit!!', res.msg);
                    return false;
                     _this.$myLayer.successLayer(res.msg)
                }
              }) 
        },
        

        getInfoData(){ 
            let params={id:this.ruleForm.id}
            this.$api.getIdData(params).then(res=>{            
                   if(res.success==true){
                      this.bookinfodata=res.data;
                      this.treestate=res.data.status;
                      this.uservalue=res.data.contentName;
                    console.log(res.data.sheetsType)
                      this.ruleForm.phasevalue=res.data.periodId;//学段
                      this.ruleForm.subjectValue=res.data.subjectId;
                      this.ruleForm.versionValue=res.data.textbookVersionId;
                      this.ruleForm.gradeValue=res.data.gradeId;
                      this.ruleForm.volumeValue="" + res.data.sheetsType;
                      this.ruleForm.notesValue=res.data.notes;
                   }
            })
        },

        // 第二个tab中的数据

        getapiTreeData(){
            let params={contentId:this.ruleForm.id,status:this.treestate};
            this.$api.getTreeData(params).then(res=>{
                console.log("树",res.data)
                this.treeLeftData.push(res.data)
            })
        },
       
        
        getapiTreeSelectpointData(){ 
             let params={periodCode:this.bookinfodata.periodId, subjectId:this.bookinfodata.subjectId}
              console.log("params", params)
             this.$api.getTreeSelectData(params).then(res=>{       
                console.log("知识点返回来的数据",res.data)
                if(res.success==true){
                    
                    this.knowData=res.data;
                }
            })
                 
        },

        handleClick(tab, event) {
            console.log(tab, event);
           if(tab.index==0){
                this.addFlag=false;
                this.disableFlag=false;
                this.editorFlag=true;
                if(this.editSwitch==true){
                     this.editorFlag=false;
                }
            }else if(tab.index==1){
               this.addFlag=true;
               this.disableFlag=true;
               this.editorFlag=true;

               if(this.bookcatalo==false){
                  this.editorFlag=false;   
               }
            }

        },
        
        getSuperiorData(){//textbookVersionId
            let params={contentId:this.bookinfodata.id}
            console.log(params);
           this.$api.getSuperior(params).then(res=>{
              if(res.success==true){
                  this.superiorData=res.data;
              }
               console.log("上级",res.data);
           })  
        },


      // 点击树形结构
      listTree(val) {
        if(this.bookcatalo==false){
               console.log("你点击的树形菜单", val);
               this.form.name=val.name;
               this.bookmoduleid=val.id;
        }else{
            let params = {
            id: val.id
        };
            // this.form.name=val.label;
            this.form.order=val.id; 
            console.log("你点击的树形菜单", val);
            this.getapigetBookDetail();
        }   
      },

      getapigetBookDetail(){
          let params={id:this.form.order}        
          this.$api.getBookDetail(params).then(res=>{
              console.log("-------ha",res.data);
              if(res.success==true){       

                  console.log(res)
                 this.BookDetailData=res.data;
            }
          })
      },
      


      onSave(bookmoduleId){
            console.log("this.knowData",this.knowData);
            console.log("this.valuePoint",this.valuePoint);
            //获取name
            for(let key in this.knowData){
                if(this.valuePoint[key]==this.knowData[key].id){
                        this.emtpyarr.push(this.knowData[key].name) 
                    console.log(this.emtpyarr)  
                }
            }
            console.log("搜集的数据name",this.emtpyarr);
            //组装格式
            for(let key1 in this.valuePoint){
            	for(let key2 in this.emtpyarr){
            		if(key1==key2){
            			this.pointarr.push({"knowledgePointsId":this.valuePoint[key1],"knowledgePointsName":this.emtpyarr[key2] })
            		}
            	}
            }
            console.log("等会要传递的参数",this.pointarr)
            let _this=this;
              _this.$refs[bookmoduleId].validate((valid) => {
                if (valid) {    
                    let params={
                       catalogName:_this.form.name, //目录名称
                       catalogType:_this.treeLeftData[0].pid, //类型code
                       id:_this.bookmoduleid,           
                       orderIndex: _this.form.order,
                       parentId:_this.form.superiorValue,
                       pointsId:_this.pointarr,//目录关联知识点id集合
                       textbookContentId:_this.ruleForm.id
                    }
                    console.log("提交了",params);
                    this.$api.postModuleSave(params).then(res=>{
                        console.log(res.data)
                        if(res.success==true){
                           this.$message({
                             message: '保存成功',
                             type: 'success'
                            });
                           _this.bookcatalo=true;
                        }
                    })
                    //  _this.bookcatalo=false;
                } else {
                    console.log('error submit!!');
                    return false;
                }
              }) 
      },
      //  右上角按钮中的方法
      Editor(value){
          //value==0
         this.editSwitch=true; 
         this.editorFlag=false; 
         if(this.bookcatalo==true){
            this.bookcatalo=false;
         }    
      },
      Add(){

      },

      Disable(){

      } 
  
    },
}
</script>

<style scope>
    /*表单默认宽度 */
    input.el-input__inner {
        width: 280px;
    }
    .page-template{
        padding: 18px 24px;
        height:100%;
        overflow: hidden; 
    }
    .kno-title{
        height: 32px;
        border-bottom: 1px solid #e5e7ef;
        margin-bottom: 26px;
    }
    .kno-title .bg-purple-light{
        text-align: right;
    }
    .kno-bnt{
        margin-top: 14px;
    }
    .kno-bnt .bg-purple-light{
        text-align: right;
    }
    .kno-bnt .bg-purple{
        margin-top: 4px;
    }
    .stu-yeaer-modal-add, .tu-yeaer-modal-edit {
        display: none;
    }
    .modalAdd {
        padding: 0px 32px;
        margin: 24px 0px;
    }
    .tab-box{
        height:100%;
    }
    
    /*右边标题*/
    .org-set-rnt-title{
        margin-bottom: 15px;
    }
    
   
    /*清除间距 */
    .el-form-item__content{
        margin-left:0 !important; 
    }
    
    .limitlabewidth{
        width:200px;
    }
    .saveBtn{
        margin-left:80px;
    }
    .el-form-item__error {
      margin-left: 80px;
    }    
    .icon-none span{
        display:none !important;
    }
    /*树形结构开始*/
 

  .org-set{
    display: flex;
    height: 100%;
  }

    .org-set-left {
    width: 210px;
    }
   
    
    .org-set-rnt{
        width:calc(100% - 210px) ;
        padding: 18px 24px;
    }
    .main-box {
        height: 100%;
    }

    .el-tabs__content {
       height: calc(100% - 70px);
    }

    /*end */
    .limtwidth{
        width: 200px;
        height:100%;
    }
    
    .form-save{
        margin-left:80px;
    }

    .el-button--primary>span{
       color: #fff;
    }

    .item-box{
        margin-bottom:42px;
    }

    .left-Field{
        width:46px;
        margin-right:30px;
        display:inline-block;
        text-align:right;
    }
    .right-deatil{
        height:100%;
    }

    .item-box span{
        font-size: 14px;
        color: rgba(96,98,102,1);
        height: 12px;
        opacity: 1;
        line-height: 36px;
    }

    .org-set-rnt-bott{
        width: 109px;
        position: absolute;
        top: 60px;
        right: 15px;
    }
    .empty{
        height:100%;
    }
</style>